.paralax-background{
  background-color: blue;
  width: 100%;
  height: 550px;
  position: fixed;
  top: 0px;
  z-index: -1;
}

.homepage .content-container{
  padding-top: 550px;
}

.prof-pic-container{
  width: 200px;
  height:200px;
  margin-left: 20px;
  transform: translateY(-100px);
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 1px 1px 9px;
}
.prof-pic{
  width: 100%;
  height: auto;
}

.homepage .content{
  background-color: white;
}